// import/first : 0
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { usePagination, useQueryParams, useSearchQuery } from 'shared/hooks';
import get from 'lodash/get';
import { ErrorBoundary, Loader } from 'shared/components';
import React from 'react';
import { Company } from 'modules/user/reducers';
import { IMetaAll } from 'shared/interfaces';
import { useAsStaff } from '../hooks/useAsStaff';
import BusinessTable from './BusinessTable';

function AsStaffInner() {
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const { query, setQuery, searchQuery } = useSearchQuery();
	const { tempCompanyId, handleChangeCompany } = useAsStaff();
	const { query: urlQuery } = useQueryParams();
	const setQueryHandler = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
		setQuery(event.target.value);

	return (
		// eslint-disable-next-line react/jsx-no-useless-fragment
		<>
			{companyId ? (
				<EntityContainer.All
					entity="userCompanies"
					name="AllUserCompanies"
					url="/user/companies/"
					replaceIds
					params={{
						limit: 10,
						extra: {
							search: searchQuery,
							ordering: urlQuery.ordering || ''
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const newItems = items.filter((companyItem) =>
							get(companyItem, 'company.is_business')
						) as Company[];
						return (
							<BusinessTable
								tempCompanyId={tempCompanyId}
								page={page}
								query={query}
								handlePage={handlePage}
								meta={meta as IMetaAll}
								items={newItems}
								setQueryHandler={setQueryHandler}
								handleChange={handleChangeCompany}
								isFetched={isFetched}
							/>
						);
					}}
				</EntityContainer.All>
			) : (
				<Loader />
			)}
		</>
	);
}

export default function AsStaff() {
	return (
		<ErrorBoundary>
			<AsStaffInner />
		</ErrorBoundary>
	);
}
