import React, { useState } from 'react';
import { WhiteModal, Button, DialogActions, DialogTitle } from 'shared/components/Elements';
import { ErrorBoundary, Loader } from 'shared/components';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { Company } from 'modules/user/reducers';

type Props = {
	handleModalOpen: (item: Company | null) => void;
	open: Company | null;
};

function BusinessExitModalInner({ handleModalOpen, open }: Props) {
	const [loading, setLoading] = useState(false);
	const { handleRequest: handleExitRequest } = useEntityForm({
		entity: 'userCompanies',
		name: 'AllUserCompanies',
		method: 'delete',
		url: `/user/companies/${open?.id}/left_business/${open?.company.id}/`,
		deleteData: true,
		id: open?.id,
		cb: {
			success: (data) => {
				handleModalOpen(null);
				setLoading(false);
			},
			error: (error) => {
				setLoading(false);
			}
		}
	});

	const exitRequest = () => {
		setLoading(true);
		handleExitRequest();
	};

	const handleBusinessExit = () => {
		if (open) exitRequest();
	};

	const closeModal = () => {
		handleModalOpen(null);
	};

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)}>
				<DialogTitle id="alert-dialog-title">
					Are you sure to disconnect from
					<strong> {open?.company?.name}</strong>?
				</DialogTitle>
				<DialogActions>
					<Button onClick={closeModal} autoFocus variant="contained">
						Cancel
					</Button>
					<Button onClick={handleBusinessExit} variant="contained" color="secondary">
						Continue
					</Button>
				</DialogActions>
				{loading && (
					<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
						<Loader />
					</div>
				)}
			</WhiteModal>
		</div>
	);
}

export function BusinessExitModal(props: Props) {
	return (
		<ErrorBoundary>
			<BusinessExitModalInner {...props} />
		</ErrorBoundary>
	);
}
