import React from 'react';
import { useBusinessContext } from '../contexts/BusinessContext';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { BusinessCompany } from 'modules/user/reducers';
import { ErrorBoundary, Loader } from 'shared/components';
function BusinessInfoInner() {
	const { businessId } = useBusinessContext();
	const { companyId } = useAppState();
	return (
		<div>
			<EntityContainer.One
				id={businessId}
				relations={''}
				entity={'businessCompany'}
				name={`businessCompany-${businessId}`}
				url={`/${companyId}/business_companies/${businessId}/`}
				params={{}}
			>
				{({ item, isFetched }) => {
					const businessItem = item as BusinessCompany;
					return (
						<>
							<div className="p-4">
								{isFetched ? (
									<div>
										<h1 className={'mb-2'}>{businessItem?.name}</h1>
										<div>{businessItem?.about}</div>
									</div>
								) : (
									<Loader />
								)}
							</div>
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}

export function BusinessInfo() {
	return (
		<ErrorBoundary>
			<BusinessInfoInner />
		</ErrorBoundary>
	);
}
