import React, { useState } from 'react';
import { TabPanel, CustomTab, CustomTabs } from 'shared/components/Elements';
import { ErrorBoundary } from 'shared/components';
import { GrFacebookOption } from 'react-icons/gr';
import { FiExternalLink } from 'react-icons/fi';
import { RiTwitterLine } from 'react-icons/ri';
import { AiFillPhone } from 'react-icons/ai';
import { BusinessInfo } from './BusinessInfo';
import { BusinessDepartments } from './BusinessDepartments';
import { BusinessLocations } from './BusinessLocations';
import { useBusinessContext } from '../contexts/BusinessContext';

function BusinessViewInner() {
	const [tabValue, setTabValue] = useState(0);
	const { businessId, viewedBusiness } = useBusinessContext();
	// eslint-disable-next-line @typescript-eslint/ban-types
	const handleChangeTab = (event: React.ChangeEvent<{}>, newValue: number) => {
		setTabValue(newValue);
	};

	return (
		<div className="bus-view">
			<div className="bus-view__tabs">
				<CustomTabs value={tabValue} onChange={handleChangeTab} aria-label="Kennekt custom">
					<CustomTab label="Info" />
					<CustomTab label="Call us" />
					<CustomTab label="Locations_depr" />
				</CustomTabs>
			</div>
			<div className="bus-view__content">
				{businessId && (
					<>
						<TabPanel value={tabValue} index={0}>
							<BusinessInfo />
						</TabPanel>
						<TabPanel value={tabValue} index={1}>
							<BusinessDepartments />
						</TabPanel>
						<TabPanel value={tabValue} index={2}>
							<BusinessLocations />
						</TabPanel>
					</>
				)}
			</div>
			<div className="bus-view__footer flex items-center justify-center">
				{viewedBusiness.company?.facebook_url && (
					<a
						href={
							viewedBusiness.company?.facebook_url.startsWith('https')
								? viewedBusiness.company?.facebook_url
								: `https://${viewedBusiness.company?.facebook_url}`
						}
						target="_blank"
						className="flex-centered group mr-2 h-8 w-8 cursor-pointer rounded-full border border-[#1877f2] bg-[#1877f2] transition hover:bg-white"
						rel="noreferrer"
					>
						<div className="icon text-lg text-white group-hover:text-[#1877f2]">
							<GrFacebookOption />
						</div>
					</a>
				)}

				{viewedBusiness.company?.twitter_url && (
					<a
						href={
							viewedBusiness.company?.twitter_url.startsWith('https')
								? viewedBusiness.company?.twitter_url
								: `https://${viewedBusiness.company?.twitter_url}`
						}
						target="_blank"
						className="flex-centered group mr-2 h-8 w-8 cursor-pointer rounded-full border border-[#1d9bf0] bg-[#1d9bf0] transition hover:bg-white"
						rel="noreferrer"
					>
						<div className="icon text-lg text-white group-hover:text-[#1d9bf0]">
							<RiTwitterLine />
						</div>
					</a>
				)}
				{viewedBusiness.company?.website && (
					<a
						href={
							viewedBusiness.company?.website.startsWith('https')
								? viewedBusiness.company?.website
								: `https://${viewedBusiness.company?.website}`
						}
						target="_blank"
						className="flex-centered group mr-2 h-8 w-8 cursor-pointer rounded-full border border-primary bg-primary transition hover:bg-white"
						rel="noreferrer"
					>
						<div className="icon text-lg text-white group-hover:text-primary">
							<FiExternalLink />
						</div>
					</a>
				)}
				{viewedBusiness.company?.phone && (
					<a
						href={`tel:${viewedBusiness.company?.phone}`}
						className="flex-centered group mr-2 h-8 w-8 cursor-pointer rounded-full border border-primary bg-primary transition hover:bg-white"
					>
						<div className="icon text-lg text-white group-hover:text-primary">
							<AiFillPhone />
						</div>
					</a>
				)}
			</div>
		</div>
	);
}

export function BusinessView() {
	return (
		<ErrorBoundary>
			<BusinessViewInner />
		</ErrorBoundary>
	);
}
