import React, { useState } from 'react';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { ErrorBoundary, Loader } from 'shared/components';
import { usePagination } from 'shared/hooks';

import Icons from 'shared/components/Elements/MuiIcons';
import {
	Button,
	TableContainer,
	Table,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Pagination
} from 'shared/components/Elements';
import SlidingPane from 'react-sliding-pane';
import { Locations } from 'shared/components';
import { useBusinessContext } from '../contexts/BusinessContext';
import 'react-sliding-pane/dist/react-sliding-pane.css';

export type Branch = {
	id: number;
	company: {
		id: number;
		name: string;
		color: string;
	};
	isMain?: boolean;
	name: string | null;
	zipcode: string | null;
	city: string | null;
	state_name: string | null;
	country_name: string | null;
	address: string | null;
	address_2: string | null;
	country: string | null;
	state: string | null;
	phone: string | null;
	lat: string | null;
	lng: string | null;
};

function BusinessLocationsInner() {
	const { businessId, viewedBusiness } = useBusinessContext();
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination('branchPage');

	const [state, setState] = useState({
		isPaneOpen: false
	});

	return (
		<div>
			<EntityContainer.All
				entity="branches"
				name={`branches-${businessId}`}
				url={`/${companyId}/companies/${businessId}/branches/`}
				params={{
					limit: 10,
					page
				}}
			>
				{({ items, meta, isFetched }) => {
					const branches = [...items].filter((item: any) => item.lat && item.lng) as Branch[];
					return (
						<div className="p-4">
							<div className="mb-4">
								<Button
									color="primary"
									variant="contained"
									disabled={!branches.length}
									onClick={() => setState({ isPaneOpen: true })}
								>
									{branches.length ? 'Show map' : 'No locations available'}
								</Button>
							</div>
							<SlidingPane
								className="some-custom-class"
								overlayClassName="some-custom-overlay-class"
								isOpen={state.isPaneOpen}
								title="Map"
								from="bottom"
								width="100%"
								onRequestClose={() => {
									setState({ isPaneOpen: false });
								}}
							>
								<Locations branches={branches} currentCompany={viewedBusiness} />
							</SlidingPane>
							{isFetched ? (
								<div>
									<TableContainer>
										<Table
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<TableRow>
													<TableCell>Branch</TableCell>
													<TableCell>Action</TableCell>
												</TableRow>
											</TableHead>
											{isFetched ? (
												<TableBody>
													{items.map((item: any) => {
														return (
															<TableRow key={item.id}>
																<TableCell>{item.name}</TableCell>
																<TableCell>
																	<a href={`tel:${item.phone}`}>
																		<Icons.IconButton color="primary" aria-label="visibility">
																			<Icons.PhoneIcon />
																		</Icons.IconButton>
																	</a>
																</TableCell>
															</TableRow>
														);
													})}
												</TableBody>
											) : (
												<Loader />
											)}
										</Table>
									</TableContainer>
									<div className="mt-4 mb-4">
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											onChange={handlePage}
											color="primary"
										/>
									</div>
								</div>
							) : (
								<Loader />
							)}
						</div>
					);
				}}
			</EntityContainer.All>
		</div>
	);
}

export function BusinessLocations() {
	return (
		<ErrorBoundary>
			<BusinessLocationsInner />
		</ErrorBoundary>
	);
}
