import React from 'react';
import { useBusinessContext } from '../contexts/BusinessContext';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { Loader } from 'shared/components';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableBody from '@material-ui/core/TableBody';
import IconButton from '@material-ui/core/IconButton';
import Pagination from '@material-ui/lab/Pagination';
import { usePagination } from 'shared/hooks';
import PhoneIcon from '@material-ui/icons/Phone';
import { ErrorBoundary } from 'shared/components';

export type Department = {
	id: number;
	name: string;
	phone: string | null;
};

function BusinessDepartmentsInner() {
	const { businessId } = useBusinessContext();
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination('callPage');

	return (
		<div>
			<EntityContainer.All
				entity={'businessDepartments'}
				name={`businessDepartments-${businessId}`}
				url={`/${companyId}/business_companies/${businessId}/departments/`}
				params={{
					limit: 5,
					page
				}}
				replaceIds
			>
				{({ items, meta, isFetched }) => {
					const departments = items.filter((item: any) => item.phone) as Department[];
					return (
						<>
							<div className="p-4">
								{isFetched ? (
									<div>
										<TableContainer>
											<Table
												aria-label="simple table"
												classes={{ root: 'table-fixed dark-mode-styled-table' }}
											>
												<TableHead>
													<TableRow>
														<TableCell>Business</TableCell>
														<TableCell>Action</TableCell>
													</TableRow>
												</TableHead>
												{isFetched ? (
													<TableBody>
														{departments.map((item: Department) => {
															return (
																<TableRow key={item.id}>
																	<TableCell>{item.name}</TableCell>
																	<TableCell>
																		<a href={`tel:${item.phone}`}>
																			<IconButton color="primary" aria-label="visibility">
																				<PhoneIcon />
																			</IconButton>
																		</a>
																	</TableCell>
																</TableRow>
															);
														})}
													</TableBody>
												) : (
													<Loader />
												)}
											</Table>
										</TableContainer>
										<div className={'mt-4 mb-4'}>
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												onChange={handlePage}
											/>
										</div>
									</div>
								) : (
									<Loader />
								)}
							</div>
						</>
					);
				}}
			</EntityContainer.All>
		</div>
	);
}

export function BusinessDepartments() {
	return (
		<ErrorBoundary>
			<BusinessDepartmentsInner />
		</ErrorBoundary>
	);
}
