import React, { useState, useEffect } from 'react';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { Company } from 'modules/user/reducers';
import { IMetaAll } from 'shared/interfaces';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableRow,
	StyledTableHeadCell
} from 'shared/components';
import {
	TableContainer,
	WhiteModal,
	TableHead,
	TableBody,
	Radio,
	Pagination,
	TableSortLabel,
	Tooltip,
	Avatar
} from 'shared/components/Elements';
import Icons from 'shared/components/Elements/MuiIcons';
import { isMobile } from 'react-device-detect';

import { useQueryParams, useRouter } from 'shared/hooks';
import { ParsedQuery } from 'query-string';
import config from 'config';
import clsx from 'clsx';
import { IconPin, IconSwitchBSort } from 'shared/icons';
import { SwitchBusinessSearch } from './SwitchBusinessSearch';
import { useBusinessContext } from '../contexts/BusinessContext';
import { BusinessView } from './BusinessView';
import { IconUnpinned } from 'shared/icons/IconUnpinned';
import { useAsStaff } from '../hooks/useAsStaff';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { BusinessExitModal } from './BusinessExitModal';
import { useAppState } from 'shared/state';

const imgPath = `${config.PUBLIC_ASSETS}img`;

function checkOrderType(field: string, query: ParsedQuery) {
	const { ordering } = query;

	if (!ordering) return field;
	if (ordering.includes('-') && ordering.includes(field)) return field;
	if (ordering.includes(field)) return `-${field}`;

	return field;
}

const useStyles = makeStyles(
	createStyles({
		radioCell: {
			width: '50px'
		},
		radioRoot: {
			padding: isMobile ? '0px' : '9px'
		},
		tdRoot: {
			padding: isMobile ? '8px' : '16px'
		},
		thead: {
			'& th:first-child': {
				borderRadius: '10px',
				backgroundColor: 'yellow'
			}
		}
	})
);

type Props = {
	items: Company[];
	meta: IMetaAll;
	handleChange: (item: Company) => void;
	tempCompanyId: number | null;
	page: number | undefined;
	handlePage: (event: React.ChangeEvent<unknown>, value: number) => void;
	query: string;
	setQueryHandler: (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => void;
	isFetched: boolean;
};

function BusinessTable({
	items,
	meta,
	handleChange,
	tempCompanyId,
	page,
	handlePage,
	query,
	setQueryHandler,
	isFetched
}: Props) {
	const classes = useStyles();
	const { setBusinessId, setBusiness } = useBusinessContext();
	const [open, setOpen] = useState(false);
	const handleModalOpen = () => setOpen((prev) => !prev);
	const { handleSort, query: urlQuery, removeQueryParams } = useQueryParams();
	const { pinUnpinHandler, disconnectHandler, openExitModal, setOpenExitModal } = useAsStaff();
	const { userData } = useAuth();
	const { isDarkMode } = useAppState();
	const { history } = useRouter();

	useEffect(() => {
		// Business id in context must be set to null if View modal is hidden;
		if (!open) {
			setBusinessId(null);
			setBusiness(null);
		}
	}, [open, setBusinessId, setBusiness]);

	return (
		<div>
			<TableContainer classes={{ root: 'relative' }}>
				<StyledTable
					aria-label="simple table"
					classes={{ root: 'table-fixed dark-mode-styled-table' }}
				>
					<TableHead className="custom-kennekt-thead">
						<StyledTableRow>
							<StyledTableHeadCell style={{ width: '5%' }}>
								{urlQuery.ordering && (
									<Tooltip title="Reset sort to default" placement="top">
										<Icons.IconButton
											size="small"
											onClick={() => removeQueryParams()}
											color="default"
											className="hover:text-kprimary"
										>
											<div className="default-sort-img h-8 w-8">
												<img src={`${imgPath}/icon-default-sort.svg`} alt="" />
											</div>
										</Icons.IconButton>
									</Tooltip>
								)}
							</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '20%' }}>
								<TableSortLabel
									IconComponent={IconSwitchBSort}
									active={urlQuery.ordering?.includes('company__name')}
									direction={
										urlQuery.ordering === 'company__name' || !urlQuery.ordering ? 'desc' : 'asc'
									}
									onClick={() => handleSort(checkOrderType('company__name', urlQuery), 'ordering')}
								>
									<span>Business name</span>
								</TableSortLabel>
							</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '10%' }}>
								<TableSortLabel
									IconComponent={IconSwitchBSort}
									active={urlQuery.ordering?.includes('company__unread_case_count')}
									direction={
										urlQuery.ordering === 'company__unread_case_count' || !urlQuery.ordering
											? 'desc'
											: 'asc'
									}
									onClick={() =>
										handleSort(checkOrderType('company__unread_case_count', urlQuery), 'ordering')
									}
								>
									<span>Unread</span>
								</TableSortLabel>
							</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '10%' }}>Overdue cases</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '10%' }}>Staff</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '10%' }}>Status</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '10%' }}>Actions</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '25%' }}>
								<SwitchBusinessSearch
									handleSearch={setQueryHandler}
									query={query}
									placeholder="Search for business"
								/>
							</StyledTableHeadCell>
						</StyledTableRow>
					</TableHead>
					<TableBody>
						{items.map((item: Company) => {
							const isOwner = item.company.owner?.id === userData.id;
							const {
								name,
								id: companyId,
								status,
								unread_cases_count,
								overdue_cases_count,
								color,
								icon,
								icon_dark,
								users_count
							} = item.company;
							const isSelected = Number(tempCompanyId) === companyId;

							const iconUrl = isDarkMode
								? icon_dark
									? icon_dark?.thumbnail_150
									: icon?.thumbnail_150
								: icon?.thumbnail_150;

							const handleBusinessSelect = (item: Company) => () => {
								//forward to case messages page if user clicks on active selected business
								if (companyId === tempCompanyId) {
									history.push('/case-messages');
								} else {
									handleChange(item);
								}
							};

							return (
								<StyledTableRow
									key={item.id}
									className={clsx({
										'pointer-events-none opacity-50': status === 'INACTIVE'
									})}
								>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<Radio
											color="primary"
											checked={isSelected}
											classes={{ root: classes.radioRoot }}
										/>
									</StyledTableCell>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<div className="flex items-center">
											<Avatar
												src={iconUrl}
												letter={name[0]}
												alt={name}
												className={`bg-[${color}] mr-2`}
												classes={{ root: 'w-8 h-8' }}
											/>
											<span>{name}</span>
										</div>
									</StyledTableCell>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<span
											className={clsx('font-semibold', {
												'flex-centered h-8 w-8 rounded-full bg-primary text-white':
													unread_cases_count > 0
											})}
										>
											{unread_cases_count}
										</span>
									</StyledTableCell>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<span
											className={clsx('font-semibold', {
												'flex-centered h-8 w-8 rounded-full bg-primary text-white':
													overdue_cases_count > 0
											})}
										>
											{overdue_cases_count}
										</span>
									</StyledTableCell>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<span className="font-semibold">{users_count}</span>
									</StyledTableCell>
									<StyledTableCell onClick={handleBusinessSelect(item)}>
										<span
											className={clsx(
												'block h-[20px] w-[90px] rounded-[100px] bg-kgrey-inputBd text-center text-xs font-medium uppercase leading-5 dark:bg-primary',
												{
													'dark:!bg-kgrey': status === 'INACTIVE'
												}
											)}
										>
											{status}
										</span>
									</StyledTableCell>
									<StyledTableCell>
										<Tooltip
											title={`${item.pin_time ? 'Unpin business' : 'Pin business'}`}
											placement="top"
										>
											<Icons.IconButton
												size="small"
												onClick={() => pinUnpinHandler(item)}
												color="default"
												className="!mx-2 hover:text-primary"
											>
												{item.pin_time ? (
													<IconPin className="text-kgrey transition hover:text-primary" />
												) : (
													<IconUnpinned className="text-kgrey transition hover:text-primary" />
												)}
											</Icons.IconButton>
										</Tooltip>
										<Tooltip
											title={
												isOwner
													? "You can't disconnect from your own business"
													: `Disconnect from ${name}`
											}
											placement="top"
										>
											<Icons.IconButton
												size="small"
												onClick={() =>
													(!isOwner || isSelected) && disconnectHandler({ item, isOwner })
												}
												color="secondary"
												className="ml-10"
											>
												<div className={clsx('h-5 w-5', { 'opacity-50': isOwner })}>
													<img src={`${imgPath}/icon-left-business.svg`} alt="" />
												</div>
											</Icons.IconButton>
										</Tooltip>
									</StyledTableCell>
									<StyledTableCell />
								</StyledTableRow>
							);
						})}
					</TableBody>
				</StyledTable>
				{!isFetched && <Loader tiled withOverlay />}
			</TableContainer>
			<div className="mt-4 mb-4">
				<Pagination
					count={meta?.last_page}
					page={page || 1}
					onChange={handlePage}
					defaultPage={6}
					siblingCount={1}
					color="primary"
				/>
			</div>
			<WhiteModal handleModalOpen={handleModalOpen} open={Boolean(open)}>
				<BusinessView />
			</WhiteModal>
			<BusinessExitModal handleModalOpen={setOpenExitModal} open={openExitModal} />
		</div>
	);
}

export default function BusinessTableWithErrorBoundary(props: Props) {
	return (
		<ErrorBoundary>
			<BusinessTable {...props} />
		</ErrorBoundary>
	);
}
